import React,{useState} from 'react';
import './Header.css';
import {Link} from 'react-router-dom';
import {GoThreeBars} from 'react-icons/go';
import {AiOutlineCloseCircle} from 'react-icons/ai';
import {TopNavData} from './TopNavData';
import {useSelector, useDispatch} from 'react-redux';
import { SignOut } from '../store/Actions/UserActions';
import {TiShoppingCart} from 'react-icons/ti';

function Header({sidebar, setsidebar}) {

    const showsidebar = ()=> setsidebar(!sidebar);

    const dispatch = useDispatch();

    const Cart = useSelector(state=> state.Cart);
    const {CartItems}= Cart;
    
    const UserSign = useSelector(state=> state.UserSign);
    const {UserInfo}= UserSign;

    const SignOutHandler=()=>{
        dispatch(SignOut());
    }

    return (
        <div>
            <div className='top-header-container'>
            <div className='logo-container'>
                <Link to='/'><img className='amazon-logo' src='/Images/amzlogo.png'/></Link>
            </div>

            <div className='search-bar-container'>
                <input className='main-search-bar' placeholder='Seach Over 10,000 + Products'/>
            </div>

            <div className='signin-cart-container'>
                <TiShoppingCart className='cart-icon'/>
                {UserInfo ?
                (<div>
                    <div><Link className='sign-links'>My Account</Link></div>
                </div>)
            : (<div style={{display: 'flex', gap: '1.5rem', alignItems: 'center'}}>
                <div><Link className='sign-links' to='/signin'>Sign In</Link></div>
                <div><Link className='sign-links' to='/register'>Register</Link></div> 
            </div>)}


            </div>

            </div>

            <div className='second-header-container'>
                <div className='menu-bar-container' onClick={showsidebar}>
               {sidebar ?  <AiOutlineCloseCircle className='menu-bar'/> :
                <GoThreeBars className='menu-bar'/>}
                </div>

                <div className='top-nav-data-container'>
                {TopNavData.map((navdata, index) =>{
                return(
                 <div className='navi-item'><Link key={index} to={navdata.Path} 
                 className={navdata.CName}>{navdata.Title}</Link></div>
                 );
                })}</div>


            </div>
            <div className='top-divider'>
            <marquee width="60%" direction="left" width="100%">10% Discounts for All Products Above Rs 2500 
            {" / "}20% Discounts for All Products Above Rs 5000</marquee>
            </div>
            
          </div>
    )
}

export default Header;
