import React from 'react';
import {Link} from 'react-router-dom';
import './Products.css';
import formatCurrency from '../Currency';
import {Card, Button, CardMedia, Typography, CardActionArea, CardActions, CardContent} from '@material-ui/core';

export default function Products (props){
    const { product } = props;

   

        return (
            <Card className="single-card" key={product._id}>
            <Link style={{textDecoration: 'none', color: '#1B262C'}} to={`/product/${product._id}`}>
            <CardActionArea>
              <CardMedia
              className='card-img'
                component="img"
                height="140"
                image={product.Image}
                title={product.Title}/>
              <CardContent>
                <Typography style={{fontSize: '18px', paddingBottom: '1rem', fontWeight: "500"}}>
                {(product.Title.substr(0,30))}{"..."}
                </Typography>
                <Typography variant="body2" style={{fontSize:'13px'}} color="textSecondary" component="p">
            {(product.Description.substr(0,50))}{"..."}</Typography>
              </CardContent>
            </CardActionArea>
            <CardActions >
              <Button size="large" color="primary"><Link style={{textDecoration: "none"}} to={`/product/${product._id}`}>View</Link></Button>
              <Button size="large" color="secondary" >{formatCurrency(product.Price)}</Button>
            </CardActions>
            </Link>
            </Card>
        )
    
}